<template>
  <v-dialog v-model="vModel" :max-width="isTextbox ? '600px' : '300px'" :scrollable="scrollable">
    <v-card>
      <v-card-title>
        <span class="headline">{{title}}</span>
      </v-card-title>
      <v-card-text :style="{height: isSelect ? '300px' : undefined }">
        <slot></slot>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="accent" flat @click.native="vModel = false">Close</v-btn>
        <!-- <v-btn color="accent" flat @click.native="vModel = false">Save</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    form: {
      type: String,
      validator: v => ["textbox", "select"].indexOf(v) !== -1
    },
    title: {
      type: String,
      default: ""
    },
    scrollable: {
      type: Boolean,
      default: false
    },
    value: {
      required: true
    }
  },
  computed: {
    isTextbox() {
      return this.form === "textbox";
    },
    isSelect() {
      return this.form === "select";
    },
    vModel: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      }
    }
  }
};
</script>
